    .titulo-segundo.color-acento-contenido
      h2 1.4 Elementos del plan de implementacin
    .row.align-items-center.mb-4
      .col-lg-8.mb-4.mb-lg-0(data-aos="fade-right")
        .row.justify-content-center.mb-3
          .col-lg-auto.mb-4.mb-lg-0.pe-lg-0
            figure
              img(src='@/assets/curso/tema1/img13.svg', style="width: 140px").m-auto
          .col-lg.bg-c2
            .row.h-100.align-items-center
              .col-12
                .p-4
                  p.mb-0 El diagnstico inicial constituye la base fundamental del plan de implementacin, ya que permite conocer de forma precisa, objetiva y verificable las condiciones reales en que se encuentran los equinos, el personal, las instalaciones y los procedimientos de manejo. Este anlisis integral identifica las brechas en el cumplimiento normativo, en las Buenas Prcticas Equinas y en los principios de bienestar animal. As, se establecen prioridades de intervencin y los ajustes operativos necesarios.
        p Este diagnstico debe ser realizado por personal competente y debe contar con registro documental de todos los hallazgos, clasificados como crticos, moderados o de mejora continua, lo cual facilita la toma de decisiones, la asignacin de recursos y la planificacin del cronograma de acciones correctivas.
        p.mb-0 A continuacin, se describen los componentes clave del diagnstico inicial:
      .col-lg-4(data-aos="fade-left")
        figure
          img(src='@/assets/curso/tema1/img17.png', style="width: 400px").m-auto
    .row.bg-c8
      .col-12
        .p-5
          .tarjeta.tarjeta--azul1.p-4.shadow
            SlyderA(tipo="b")
              .row.justify-content-center.align-items-center.my-lg-4
                .col-lg-5.mb-4.mb-lg-0
                  figure
                    img(src='@/assets/curso/tema1/img18.png', style="width: 400px").m-auto
                .col-lg-7
                  p #[b a) Instalaciones]
                  p Se evalan todas las reas donde permanecen o transitan los equinos para garantizar condiciones seguras, higinicas y adecuadas para su bienestar. Se revisan:
                  ul.lista-ul.mb-0
                    li.d-flex
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Pesebreras y boxes:] dimensiones, ventilacin, iluminacin, temperatura, humedad, limpieza, camas, drenaje y eliminacin de olores.
                    li.d-flex
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Corrales, establos y zonas de trabajo:] pisos antideslizantes, materiales, seguridad estructural, ausencia de elementos filosos.
                    li.d-flex
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Potreros y praderas:] calidad del pasto, rotacin, drenaje, lodo, sombras, riego, estado de cercas.
                    li.d-flex
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b reas de ejercicio y adiestramiento:] picaderos, pistas, caminadores, rondadores, zonas de calentamiento/enfriamiento.
                    li.d-flex
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Equipamiento fijo:] comederos, bebederos, saladeros, tanques de agua, almacenamiento de alimentos.
                    li.d-flex.mb-0
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Seguridad general:] sealizacin, orden, accesos, circulacin vehicular, cuarentena, manejo de nuevos ingresos.
              .row.justify-content-center.align-items-center.my-lg-4
                .col-lg-5.mb-4.mb-lg-0
                  figure
                    img(src='@/assets/curso/tema1/img19.png', style="width: 400px").m-auto
                .col-lg-7
                  p #[b b) Condicin sanitaria]
                  p Evala la salud general del hato equino y la eficacia del plan sanitario:
                  ul.lista-ul.mb-0
                    li.d-flex
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Historial clnico:] clicos, cojeras, enfermedades infecciosas, parasitarias o respiratorias.
                    li.d-flex
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Vacunacin y desparasitacin:] frecuencia, cumplimiento, productos utilizados y registros.
                    li.d-flex
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Bioseguridad:] protocolos de ingreso/salida, aislamiento, desinfeccin de herramientas, vehculos, etc.
                    li.d-flex
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Control de vectores:] presencia de moscas, mosquitos, garrapatas; uso de trampas o tratamientos.
                    li.d-flex
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Acceso a servicios veterinarios:] atencin de urgencias, visitas programadas, supervisin general.
                    li.d-flex.mb-0
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Registro de tratamientos:] control de medicamentos, fechas, dosis, prescripciones y periodos de retiro.
              .row.justify-content-center.align-items-center.my-lg-4
                .col-lg-5.mb-4.mb-lg-0
                  figure
                    img(src='@/assets/curso/tema1/img20.png', style="width: 400px").m-auto
                .col-lg-7
                  p #[b c) Condicin corporal y estado fsico]
                  p Se analiza el bienestar individual del animal mediante indicadores objetivos:
                  ul.lista-ul.mb-0
                    li.d-flex
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Condicin corporal (BCS):] uso de escalas (1-9 o 1-5).
                    li.d-flex
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Cascos:] equilibrio, desgaste, grietas, herrajes, recortes.
                    li.d-flex
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Dentadura:] desgaste, puntas, dificultad para masticar, prdida de peso.
                    li.d-flex
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Piel y pelaje:] heridas, hongos, dermatitis, mudas anormales, lesiones.
                    li.d-flex
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Locomocin:] cojera, rigidez, dolor al movimiento, desempeo en el trabajo.
                    li.d-flex
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Comportamiento y emocionalidad:] estrs, agresividad, apata, estereotipias.
                    li.d-flex.mb-0
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Estado general:] hidratacin, respiracin, mucosas, frecuencia cardiaca, condicin reproductiva.
              .row.justify-content-center.align-items-center.my-lg-4
                .col-lg-5.mb-4.mb-lg-0
                  figure
                    img(src='@/assets/curso/tema1/img21.png', style="width: 400px").m-auto
                .col-lg-7
                  p #[b d) Forma de trabajo]
                  p Analiza las condiciones laborales de cada equino:
                  ul.lista-ul.mb-0
                    li.d-flex
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Finalidad:] deporte, carga, turismo, equinoterapia, exhibicin, reproduccin, recreacin.
                    li.d-flex
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Rutinas:] duracin de jornadas, intensidad del ejercicio, descansos.
                    li.d-flex
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Agua y alimentacin:] acceso y calidad durante la jornada.
                    li.d-flex
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Equipos:] monturas, cabezadas, colleras, arneses; ajuste y estado.
                    li.d-flex
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Riesgos laborales:] superficies, temperatura, carga fsica o tcnica.
                    li.d-flex.mb-0
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Desempeo y fatiga:] signos de agotamiento, prdida de condicin, lesiones frecuentes.
              .row.justify-content-center.align-items-center.my-lg-4
                .col-lg-5.mb-4.mb-lg-0
                  figure
                    img(src='@/assets/curso/tema1/img22.png', style="width: 400px").m-auto
                .col-lg-7
                  p #[b e) Nivel de formacin del personal]
                  p Evala la competencia del personal responsable del manejo y cuidado:
                  ul.lista-ul.mb-0
                    li.d-flex
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Manejo seguro y emptico.]
                    li.d-flex
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Deteccin de signos de dolor, enfermedad o estrs.]
                    li.d-flex
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Capacitacin en primeros auxilios y emergencias.]
                    li.d-flex
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Uso correcto de herramientas y EPP.]
                    li.d-flex
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Aplicacin de protocolos de bioseguridad.]
                    li.d-flex
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Formacin normativa:] Resolucin ICA 136 de 2020 y complementarias.
                    li.d-flex.mb-0
                      i.fa-solid.fa-circle-right
                      |
                      p.mb-0 #[b Responsabilidad y disciplina:] puntualidad, registros, comunicacin.
    .row.bg-c9.mb-4
