            h2 1.7 Metodologas de desarrollo y programacin
          .row.align-items-center.mb-5
            .col-lg-8.mb-4.mb-lg-0(data-aos="fade-right")
              p La implementacin de las Buenas Prcticas Ganaderas (BPG) en la produccin primaria de quidos es un proceso progresivo y estructurado que tiene como finalidad garantizar el bienestar animal, la bioseguridad, la inocuidad de los productos, la sostenibilidad ambiental y la seguridad del personal involucrado.
              .row.justify-content-center.mb-3
                .col-lg-auto.mb-4.mb-lg-0.pe-lg-0
                  figure
                    img(src='@/assets/curso/tema1/img28.svg', style="width: 140px").m-auto
                .col-lg.bg-c2
                  .row.h-100.align-items-center
                    .col-12
                      .p-4
                        p.mb-0 Este proceso busca consolidar un sistema equino organizado, seguro y alineado con los estndares exigidos por el ICA, con miras a la certificacin oficial en BPG, conforme a la Resolucin 7953 de 2017. La metodologa de desarrollo se fundamenta en siete etapas que permiten diagnosticar el estado actual del predio, identificar brechas, formular planes de mejora, ejecutar acciones correctivas y solicitar la auditora oficial.
            .col-lg-4(data-aos="fade-left")
