        p.mb-0 Estas listas deben estar alineadas con los estndares oficiales definidos por el ICA, particularmente los contenidos en la Resolucin 7953 de 2017, la Resolucin 136 de 2020 y las guas tcnicas de bienestar animal en quidos. Para su aplicacin, es fundamental contar con el criterio tcnico del profesional responsable, quien debe evaluar con objetividad si cada tem cumple con lo estipulado.
